    export default {
    props: {
        disabled: Boolean,
        type: String,
        round: Boolean,
        theme: Object,
        height: String,
        fontSize: String,
        width: String,
        color: String,
        size: String,
        background: String,
        form: Boolean,
        arrangement: String,
        roundSize: String,
        padding: String,
        borderColor: String,
    },
    data() {
        return {
            touch: false
        }
    },
    methods: {
        handleClick(e) {
            this.$emit('click', e);
        }
    },
    computed: {
        setHeight: function() {
            if (this.height) {
                return this.height;
            } else {
                switch (this.size) {
                    case 'large':
                        return 100;
                    case 'small':
                        return 60;
                    case 'medium':
                        return 80;
                    default:
                        return 80;
                }
            }
        }
    }
}
